import './MovieCard.scss';

interface MovieCardProps {
	imageURL: string;
	title: string;
}

export const MovieCard = ({ imageURL, title }: MovieCardProps) => {
	if (!imageURL) {
		return <>'Loading...'</>;
	}
	return (
		<section className="card">
			<img src={imageURL} alt="" />
			<h3 className="card-title">{title}</h3>
		</section>
	);
};
