import { MovieLayout } from 'components/Layouts';
import { movieListByPageId } from 'services/genreService';
import { MovieListResponse } from '../types';
import { useState, useEffect } from 'react';
import { API_URL } from 'config';
import { MovieCard } from 'components/Movie/MovieCard';
import '../styles/GenreMovieList.scss';

export const GenreMovieList: React.FC = () => {
	const [movieList, setMovieList] =
		useState<MovieListResponse | null>(null);

	useEffect(() => {
		movieListByPageId(1).then((movieListData: MovieListResponse) => {
			setMovieList(movieListData);
			console.log(movieListData);
		});
	}, []);

	if (!movieList) {
		return <div>Loading...</div>;
	}

	return (
		<MovieLayout heading="Romantic Comedy">
			<aside className="movie-container">
				{movieList.page['content-items'].content.map(
					(movie, index) => (
						<MovieCard
							key={index}
							title={movie.name}
							imageURL={API_URL + 'images/' + movie['poster-image']}
						/>
					)
				)}
			</aside>
		</MovieLayout>
	);
};
